import Vue from 'vue';
import blockies from 'blockies';
export default Vue.extend({
    data: function () {
        return {};
    },
    props: [
        "userAddress"
    ],
    mounted: function () {
        var icon = blockies({
            seed: this.userAddress,
            size: 15,
            scale: 3,
        });
        this.$refs.blockiesCanvasContainer.appendChild(icon);
    }
});
